<template>
  <v-row class="flex-column">
    <v-col class="px-6 mb-3">
      <v-card 
        dark
        :style="!$vuetify.theme.dark ? 'background: #57707D' : 'background: #424242'"
      >
        <v-row class="pa-4">
          <v-col>
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>ID</v-list-item-title>
                <v-list-item-subtitle>1</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
