              <v-list-item-content>
                <v-list-item-title>Сумма ставок</v-list-item-title>
                <v-list-item-subtitle>4</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>Сумма выигрышей</v-list-item-title>
                <v-list-item-subtitle>1000</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </v-col>
          <v-col>
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>Прибыль</v-list-item-title>
                <v-list-item-subtitle>2000</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>Коэффициент</v-list-item-title>
                <v-list-item-subtitle>1.8</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>           
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>Подкрутка</v-list-item-title>
                <v-list-item-subtitle>Нет</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </v-col>
        </v-row>
      </v-card>
    </v-col>
    <v-col class="px-6">
      <v-card elevation="0">
        <v-card-title>
          <v-row justify="space-between">
            <v-col cols="12" sm="4">
              Информация
            </v-col>
            <v-col cols="12" sm="4">
              <v-select
                label="Категория" 
                :items="['Все', 'Автовывод', 'Ручная']"
                v-model="group_type"
              ></v-select>
            </v-col>
            <v-col cols="12" sm="4">
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Поиск"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="filteredItems"
          :search="search"
        >
          <template v-slot:item.userName="props">
            <div 
              style="cursor: pointer"
              @click="$router.push({ name: 'users', params: { id: props.item.userId }})"
            >
              <v-avatar 
                size="35" 
                class="mr-2"
              >
                <img :src="props.item.src">
              </v-avatar>
              <span>{{ props.item.userName }}</span>
            </div>
          </template>
        </v-data-table>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import gql from 'graphql-tag';

export default {
  props: {
    id: {
      type: [Number, String],
      required: true
    }
  },
  data: () => ({
    getUser: null,
    group_type: null,
    search: '',
    headers: [
      { text: 'ID', align: 'left', value: 'userId' },
      { text: 'Имя пользователя', value: 'userName' },
      { text: 'Ставка', value: 'bet' },
      { text: 'Автовывод', value: 'autoOutput' },
      { text: 'Вывел на', value: 'broughtTo' },
      { text: 'Выиграл', value: 'won' }
    ],
    items: [
      { 
        userId: 1,
        userName: 'Алекс',
        bet: 100,
        autoOutput: 'Да',
        broughtTo: 1.55,
        won: 155,
        src: 'https://cdn.vuetifyjs.com/images/john.jpg',
        category: 'Автовывод'
      },
      { 
        userId: 22,
        userName: 'Саша',
        bet: 100,
        autoOutput: 'Да',
        broughtTo: 1.55,
        won: 155,
        src: 'https://cdn.vuetifyjs.com/images/john.jpg',
        category: 'Автовывод'
      },
      { 
        userId: 4,
        userName: 'Алекс',
        bet: 100,
        autoOutput: 'Нет',
        broughtTo: 1.6,
        won: 160,
        src: 'https://cdn.vuetifyjs.com/images/john.jpg',
        category: 'Ручная'
      },
      { 
        userId: 1,
        userName: 'Алекс',
        bet: 1000,
        autoOutput: 'Да',
        broughtTo: 1.55,
        won: 1550,
        src: 'https://cdn.vuetifyjs.com/images/john.jpg',
        category: 'Автовывод'
      },
      { 
        userId: 8,
        userName: 'Иван',
        bet: 100,
        autoOutput: 'Да',
        broughtTo: 1.55,
        won: 155,
        src: 'https://cdn.vuetifyjs.com/images/john.jpg',
        category: 'Автовывод'
      },
      { 
        userId: 21,
        userName: 'Даша',
        bet: 100,
        autoOutput: 'Да',
        broughtTo: 1.55,
        won: 155,
        src: 'https://cdn.vuetifyjs.com/images/john.jpg',
        category: 'Автовывод'
      } 
    ]
  }),
  computed: {
    filteredItems() {
      return this.items.filter((item) => {
        return (this.group_type == 'Все') ? item : !this.group_type || (item.category.toLowerCase() === this.group_type.toLowerCase());
      });
    }
  },
  apollo: {
    getUser: {
      query: gql`query user($id: Int!) {
        getUser(id: $id) {
          name
        }
      }`,
      variables: {
        id: 1
      }
    }
  }
}
</script>
